




















import 'reflect-metadata';
import {Vue, Prop, Component} from "vue-property-decorator";

@Component
export default class BaseNftCard extends Vue{
    @Prop({default: false}) mini!: boolean;
    @Prop({default: false}) rawCard!: boolean;
    @Prop() utxoId!: string;
    flipped: boolean = false;

    flipCard(){
        this.flipped = !this.flipped;
    }
    mousenter(){
        if(this.rawCard) return;
        this.flipped = true;
    }

    mouseleave(){
        this.flipped = false;
    }

    transfer(ev: MouseEvent){
        ev.stopPropagation();
        this.$router.push({ path: '/wallet/transfer', query: { nft: this.utxoId } })
    }
}
